@import url(https://fonts.googleapis.com/css2?family=Fruktur&family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bigelow+Rules&display=swap);
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}

*::-moz-focus-inner {
  border: 0;
}

*:focus {
  outline: none;
}

body {
  height: 100vh;
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(135deg, #764ba2 0%, #764ba2 100%);
  /*  background-repeat: no-repeat; */
  background-size: cover;
  background-position: fixed;

}

.head-title {
  font-family: 'Bigelow Rules', cursive;
  font-size: 7rem;
  color: #0CCAC4;
  margin-bottom: .25em;
}
.card-container {
   display: flex;
   flex-direction: column;
   background-color: #95dada;
   border: 1px solid grey;
   border-radius: 5px;
   padding: 25px;
   cursor: pointer;
   -moz-osx-font-smoothing: grayscale;
   -webkit-backface-visibility: hidden;
           backface-visibility: hidden;
   transform: translateZ(0);
   transition: transform 0.25s ease-out;
}

.card-container:hover {
   transform: scale(1.05);
}

.card-list {

   width: 85vw;
   margin: 0 auto;
   display: grid;
   grid-template-columns: 1fr 1fr 1fr 1fr;
   grid-gap: 20px;
}
.search {
   height: 2%;
   width: 25%;
   -webkit-appearance: none;
   border: none;
   outline: none;
   padding: 10px;
   /*width: 150px;
   */line-height: 30px;
   margin-bottom: 30px;
   border-radius: 5px;
   border: 2px solid #95dada;
   font-family: 'Roboto',sans-serif;
   font-size: 1.8rem;
   font-weight: 500;
   color: #423E37;
   -webkit-border-radius: 5px;
   -moz-border-radius: 5px;
   -ms-border-radius: 5px;
   -o-border-radius: 5px;
}

.App {
  text-align: center;
}


